.profile-container {
    display: flex;
    justify-content: space-around;

    width: 80%;
    height: 500px;

    margin: 10vh 10%;
    margin-bottom: 75px;
}

.profile-photo {
    height: 100%;
}

.profile-name {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;

    font-size: 40px;
}

.profile-name span {
    font-size: 60px;
}

@media only screen and (max-width : 650px) {
    .profile-container{
        flex-direction: column;
        justify-content: space-between;
    }

    .profile-container img{
        max-height: 400px;
        max-width: 400px;

        margin-top: 50px;
    }
}